import _ from "lodash";

export default [
    {
        name : "Random",
        generate : (size) => _.sampleSize(_.range(1, size + 1), size)
    },
    {
        name : "Reversed",
        generate : (size) => _.range(1, size + 1).reverse()
    },
    {
        name : "Pyramid",
        generate : (size) => [..._.range(1, Math.floor(size/2) + 1), ..._.range(1, Math.ceil(size/2) + 1).reverse().map(e => e + 0.5)]
    },
    {
        name : "2 Pyramids",
        generate : (size) => {
            let step = Math.round(size/4)
            let add = size % 4 === 0 ? 1 : 0;
            return [
                ..._.range(1, step + 1),
                ..._.range(1, step + add).reverse().map(e => e + 0.25),
                ..._.range(1, step + 1).map(e => e +0.5),
                ..._.range(1, step + add).reverse().map(e => e + 0.75),
            ]
        }
    },
    {
        name : "Upside-down Pyramid",
        generate : (size) => [..._.range(1, Math.floor(size/2) + 1).reverse(), ..._.range(1, Math.ceil(size/2) + 1).map(e => e + 0.5)]
    }
]
