       height: 100%;\n        border: 1px solid @processing-color;\n        border-radius: 50%;\n        animation: antStatusProcessing 1.2s infinite ease-in-out;\n        content: '';\n      }\n    }\n    &-default {\n      background-color: @normal-color;\n    }\n    &-error {\n      background-color: @error-color;\n    }\n    &-warning {\n      background-color: @warning-color;\n    }\n\n    // mixin to iterate over colors and create CSS class for each one\n    .make-color-classes(@i: length(@preset-colors)) when (@i > 0) {\n      .make-color-classes(@i - 1);\n      @color: extract(@preset-colors, @i);\n      @darkColor: '@{color}-6';\n      &-@{color} {\n        background: @@darkColor;\n      }\n    }\n    .make-color-classes();\n\n    &-text {\n      margin-left: 8px;\n      color: @text-color;\n      font-size: @font-size-base;\n    }\n  }\n\n  &-zoom-appear,\n  &-zoom-enter {\n    animation: antZoomBadgeIn @animation-duration-slow @ease-out-back;\n    animation-fill-mode: both;\n  }\n\n  &-zoom-leave {\n    animation: antZoomBadgeOut @animation-duration-slow @ease-in-back;\n    animation-fill-mode: both;\n  }\n\n  &-not-a-wrapper {\n    .@{badge-prefix-cls}-zoom-appear,\n    .@{badge-prefix-cls}-zoom-enter {\n      animation: antNoWrapperZoomBadgeIn @animation-duration-slow @ease-out-back;\n    }\n\n    .@{badge-prefix-cls}-zoom-leave {\n      animation: antNoWrapperZoomBadgeOut @animation-duration-slow @ease-in-back;\n    }\n\n    &:not(.@{badge-prefix-cls}-status) {\n      vertical-align: middle;\n    }\n\n    .@{ant-prefix}-scroll-number {\n      position: relative;\n      top: auto;\n      display: block;\n      transform-origin: 50% 50%;\n    }\n\n    .@{badge-prefix-cls}-count {\n      transform: none;\n    }\n  }\n}\n\n@keyframes antStatusProcessing {\n  0% {\n    transform: scale(0.8);\n    opacity: 0.5;\n  }\n  100% {\n    transform: scale(2.4);\n    opacity: 0;\n  }\n}\n\n.@{number-prefix-cls} {\n  overflow: hidden;\n  &-only {\n    display: inline-block;\n    height: @badge-height;\n    transition: all @animation-duration-slow @ease-in-out;\n    > p.@{number-prefix-cls}-only-unit {\n      height: @badge-height;\n      margin: 0;\n    }\n  }\n\n  &-symbol {\n    vertical-align: top;\n  }\n}\n\n@keyframes antZoomBadgeIn {\n  0% {\n    transform: scale(0) translate(50%, -50%);\n    opacity: 0;\n  }\n  100% {\n    transform: scale(1) translate(50%, -50%);\n  }\n}\n\n@keyframes antZoomBadgeOut {\n  0% {\n    transform: scale(1) translate(50%, -50%);\n  }\n  100% {\n    transform: scale(0) translate(50%, -50%);\n    opacity: 0;\n  }\n}\n\n@keyframes antNoWrapperZoomBadgeIn {\n  0% {\n    transform: scale(0);\n    opacity: 0;\n  }\n  100% {\n    transform: scale(1);\n  }\n}\n\n@keyframes antNoWrapperZoomBadgeOut {\n  0% {\n    transform: scale(1);\n  }\n  100% {\n    transform: scale(0);\n    opacity: 0;\n  }\n}\n\n@import './ribbon';\n@import './rtl';\n","@import '../../style/themes/index';\n@import '../../style/mixins/index';\n\n@ribbon-prefix-cls: ~'@{ant-prefix}-ribbon';\n@ribbon-wrapper-prefix-cls: ~'@{ant-prefix}-ribbon-wrapper';\n\n.@{ribbon-wrapper-prefix-cls} {\n  position: relative;\n}\n\n.@{ribbon-prefix-cls} {\n  .reset-component();\n\n  position: absolute;\n  top: 8px;\n  height: 22px;\n  padding: 0 8px;\n  color: @badge-text-color;\n  line-height: 22px;\n  white-space: nowrap;\n  background-color: @primary-color;\n  border-radius: @border-radius-sm;\n\n  &-text {\n    color: @white;\n  }\n\n  &-corner {\n    position: absolute;\n    top: 100%;\n    width: 8px;\n    height: 8px;\n    color: currentColor;\n    border: 4px solid;\n    transform: scaleY(0.75);\n    transform-origin: top;\n    // If not support IE 11, use filter: brightness(75%) instead\n    &::after {\n      position: absolute;\n      top: -4px;\n      left: -4px;\n      width: inherit;\n      height: inherit;\n      color: rgba(0, 0, 0, 0.25);\n      border: inherit;\n      content: '';\n    }\n  }\n\n  // colors\n  // mixin to iterate over colors and create CSS class for each one\n  .make-color-classes(@i: length(@preset-colors)) when (@i > 0) {\n    .make-color-classes(@i - 1);\n    @color: extract(@preset-colors, @i);\n    @darkColor: '@{color}-6';\n    &-color-@{color} {\n      color: @@darkColor;\n      background: @@darkColor;\n    }\n  }\n  .make-color-classes();\n\n  // placement\n  &.@{ribbon-prefix-cls}-placement-end {\n    right: -8px;\n    border-bottom-right-radius: 0;\n    .@{ribbon-prefix-cls}-corner {\n      right: 0;\n      border-color: currentColor transparent transparent currentColor;\n    }\n  }\n\n  &.@{ribbon-prefix-cls}-placement-start {\n    left: -8px;\n    border-bottom-left-radius: 0;\n    .@{ribbon-prefix-cls}-corner {\n      left: 0;\n      border-color: currentColor currentColor transparent transparent;\n    }\n  }\n}\n",".@{badge-prefix-cls} {\n  &-rtl {\n    direction: rtl;\n  }\n\n  &-count,\n  &-dot,\n  .@{number-prefix-cls}-custom-component {\n    .@{badge-prefix-cls}-rtl & {\n      right: auto;\n      left: 0;\n      direction: ltr;\n      transform: translate(-50%, -50%);\n      transform-origin: 0% 0%;\n    }\n  }\n\n  .@{badge-prefix-cls}-rtl& .@{number-prefix-cls}-custom-component {\n    right: auto;\n    left: 0;\n    transform: translate(-50%, -50%);\n    transform-origin: 0% 0%;\n  }\n\n  &-status {\n    &-text {\n      .@{badge-prefix-cls}-rtl & {\n        margin-right: 8px;\n        margin-left: 0;\n      }\n    }\n  }\n\n  &-zoom-appear,\n  &-zoom-enter {\n    .@{badge-prefix-cls}-rtl & {\n      animation-name: antZoomBadgeInRtl;\n    }\n  }\n\n  &-zoom-leave {\n    .@{badge-prefix-cls}-rtl & {\n      animation-name: antZoomBadgeOutRtl;\n    }\n  }\n\n  &-not-a-wrapper {\n    .@{badge-prefix-cls}-count {\n      transform: none;\n    }\n  }\n}\n\n.@{ribbon-prefix-cls}-rtl {\n  direction: rtl;\n  &.@{ribbon-prefix-cls}-placement-end {\n    right: unset;\n    left: -8px;\n    border-bottom-right-radius: @border-radius-sm;\n    border-bottom-left-radius: 0