n  h5& {\n    .@{typography-prefix-cls} + & {\n      margin-top: @typography-title-margin-top;\n    }\n  }\n\n  div,\n  ul,\n  li,\n  p,\n  h1,\n  h2,\n  h3,\n  h4,\n  h5 {\n    + h1,\n    + h2,\n    + h3,\n    + h4,\n    + h5 {\n      margin-top: @typography-title-margin-top;\n    }\n  }\n\n  a&-ellipsis,\n  span&-ellipsis {\n    display: inline-block;\n  }\n\n  a&,\n  a {\n    .operation-unit();\n    text-decoration: @link-decoration;\n\n    &:active,\n    &:hover {\n      text-decoration: @link-hover-decoration;\n    }\n\n    &[disabled],\n    &.@{typography-prefix-cls}-disabled {\n      color: @disabled-color;\n      cursor: not-allowed;\n\n      &:active,\n      &:hover {\n        color: @disabled-color;\n      }\n\n      &:active {\n        pointer-events: none;\n      }\n    }\n  }\n\n  code {\n    margin: 0 0.2em;\n    padding: 0.2em 0.4em 0.1em;\n    font-size: 85%;\n    background: rgba(150, 150, 150, 0.1);\n    border: 1px solid rgba(100, 100, 100, 0.2);\n    border-radius: 3px;\n  }\n\n  kbd {\n    margin: 0 0.2em;\n    padding: 0.15em 0.4em 0.1em;\n    font-size: 90%;\n    background: rgba(150, 150, 150, 0.06);\n    border: 1px solid rgba(100, 100, 100, 0.2);\n    border-bottom-width: 2px;\n    border-radius: 3px;\n  }\n\n  mark {\n    padding: 0;\n    background-color: @gold-3;\n  }\n\n  u,\n  ins {\n    text-decoration: underline;\n    text-decoration-skip-ink: auto;\n  }\n\n  s,\n  del {\n    text-decoration: line-through;\n  }\n\n  strong {\n    font-weight: 600;\n  }\n\n  // Operation\n  &-expand,\n  &-edit,\n  &-copy {\n    .operation-unit();\n\n    margin-left: 4px;\n  }\n\n  &-copy-success {\n    &,\n    &:hover,\n    &:focus {\n      color: @success-color;\n    }\n  }\n\n  // Text input area\n  &-edit-content {\n    position: relative;\n\n    div& {\n      left: -@input-padding-horizontal - 1px;\n      margin-top: -@input-padding-vertical-base - 1px;\n      // stylelint-disable-next-line function-calc-no-invalid\n      margin-bottom: calc(1em - @input-padding-vertical-base - 1px);\n    }\n\n    &-confirm {\n      position: absolute;\n      right: 10px;\n      bottom: 8px;\n      color: @text-color-secondary;\n      pointer-events: none;\n    }\n\n    // Fix Editable Textarea flash in Firefox\n    textarea {\n      -moz-transition: none;\n    }\n  }\n\n  // list\n  ul,\n  ol {\n    margin: 0 0 1em 0;\n    padding: 0;\n\n    li {\n      margin: 0 0 0 20px;\n      padding: 0 0 0 4px;\n    }\n  }\n\n  ul {\n    list-style-type: circle;\n\n    ul {\n      list-style-type: disc;\n    }\n  }\n\n  ol {\n    list-style-type: decimal;\n  }\n\n  // pre & block\n  pre,\n  blockquote {\n    margin: 1em 0;\n  }\n\n  pre {\n    padding: 0.4em 0.6em;\n    white-space: pre-wrap;\n    word-wrap: break-word;\n    background: rgba(150, 150, 150, 0.1);\n    border: 1px solid rgba(100, 100, 100, 0.2);\n    border-radius: 3px;\n\n    // Compatible for marked\n    code {\n      display: inline;\n      margin: 0;\n      padding: 0;\n      font-size: inherit;\n      font-family: inherit;\n      background: transparent;\n      border: 0;\n    }\n  }\n\n  blockquote {\n    padding: 0 0 0 0.6em;\n    border-left: 4px solid rgba(100, 100, 100, 0.2);\n    opacity: 0.85;\n  }\n\n  // ============ Ellipsis ============\n  &-ellipsis-single-line {\n    overflow: hidden;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n\n    // https://blog.csdn.net/iefreer/article/details/50421025\n    a&,\n    span& {\n      vertical-align: bottom;\n    }\n  }\n\n  &-ellipsis-multiple-line {\n    display: -webkit-box;\n    -webkit-line-clamp: 3;\n    /*! autoprefixer: ignore next */\n    -webkit-box-orient: vertical;\n    overflow: hidden;\n  }\n}\n\n@import './rtl';\n","// =============== Common ===============\n.typography-paragraph() {\n  margin-bottom: 1em;\n}\n\n.typography-title(@fontSize; @fontWeight; @lineHeight; @headingColor; @headingMarginBottom;) {\n  margin-bottom: @headingMarginBottom;\n  color: @headingColor;\n  font-weight: @fontWeight;\n  font-size: @fontSize;\n  line-height: @lineHeight;\n}\n\n.typography-title-1() {\n  .typography-title(\n    @heading-1-size,\n    @typography-title-font-weight,\n    1.23,\n    @heading-color,\n    @typography-title-margin-bottom\n  );\n}\n.typography-title-2() {\n  .typography-title(\n    @heading-2-size,\n    @typography-title-font-weight,\n    1.35,\n    @heading-color,\n    @typography-title-margin-bottom\n  );\n}\n.typography-title-3() {\n  .typography-title(\n    @heading-3-size,\n    @typography-title-font-weight,\n    1.35,\n    @heading-color,\n    @typography-title-margin-bottom\n  );\n}\n.typography-title-4() {\n  .typography-title(\n    @heading-4-size,\n    @typography-title-font-weight,\n    1.4,\n    @heading-color,\n    @typography-title-margin-bottom\n  );\n}\n.typography-title-5() {\n  .typography-title(\n    @heading-5-size,\n    @typography-title-font-weight,\n    1.5,\n    @heading-color,\n    @typography-title-margin-bottom\n  );\n}\n","@import '../../style/themes/index';\n@import '../../style/mixins/index';\n\n@typography-prefix-cls: ~'@{ant-prefix}-typography';\n\n.@{typography-prefix-cls} {\n  &-rtl {\n    direction: rtl;\n  }\n\n  // Operation\n  &-expand,\n  &-edit,\n  &-copy {\n    .@{typography-prefix-cls}-rtl & {\n      margin-right: 4px;\n      margin-left: 0;\n    }\n  }\n\n  &-expand {\n    .@{typography-prefix-cls}-rtl & {\n      float: left;\n    }\n  }\n\n  // Text input area\n  &-edit-content {\n    div& {\n      &.@{typography-prefix-cls}-rtl {\n        right: -@input-padding-horizontal - 1px;\n        left: auto;\n      }\n    }\n\n    &-confirm {\n      .@{typography-prefix-cls}-rtl & {\n        right: auto;\n        left: 10px;\n      }\n    }\n  }\n\n  // list\n  ul,\n  ol {\n    li {\n      .@{typography-prefix-cls}-rtl& {\n        margin: 0 20px 0 0;\n        padding: 0 4px 0 0;\n      }\n    }\n  }\n}\n","@import '../../style/themes/index';\n@import '../../style/mixins/index';\n\n@upload-prefix-cls: ~'@{ant-prefix}-upload';\n@upload-item: ~'@{ant-prefix}-upload-list-item';\n@upload-picture-card-size: 104px;\n@upload-picture-card-border-style: @border-style-base;\n\n.@{upload-prefix-cls} {\n  .reset-component();\n\n  outline: 0;\n\n  p {\n    margin: 0;\n  }\n\n  &-btn {\n    display: block;\n    width: 100%;\n    outline: none;\n  }\n\n  input[type='file'] {\n    cursor: pointer;\n  }\n\n  &&-select {\n    display: inline-block;\n  }\n\n  &&-disabled {\n    cursor: not-allowed;\n  }\n\n  &&-select-picture-card {\n    width: @upload-picture-card-size;\n    height: @upload-picture-card-size;\n    margin-right: 8px;\n    margin-bottom: 8px;\n    text-align: center;\n    vertical-align: top;\n    background-color: @background-color-light;\n    border: @border-width-base dashed @border-color-base;\n    border-radius: @border-radius-base;\n    cursor: pointer;\n    transition: border-color 0.3s;\n\n    > .@{upload-prefix-cls} {\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      height: 100%;\n      text-align: center;\n    }\n\n    &:hover {\n      border-color: @primary-color;\n      .@{upload-prefix-cls}-disabled& {\n        border-color: @border-color-base;\n      }\n    }\n  }\n\n  &&-drag {\n    position: relative;\n    width: 100%;\n    height: 100%;\n    text-align: center;\n    background: @background-color-light;\n    border: @border-width-base dashed @border-color-base;\n    border-radius: @border-radius-base;\n    cursor: pointer;\n    transition: border-color 0.3s;\n\n    .@{upload-prefix-cls} {\n      padding: @padding-md 0;\n    }\n\n    &.@{upload-prefix-cls}-drag-hover:not(.@{upload-prefix-cls}-disabled) {\n      border-color: @primary-7;\n    }\n\n    &.@{upload-prefix-cls}-disabled {\n      cursor: not-allowed;\n    }\n\n    .@{upload-prefix-cls}-btn {\n      display: table;\n      height: 100%;\n    }\n\n    .@{upload-prefix-cls}-drag-container {\n      display: table-cell;\n      vertical-align: middle;\n    }\n\n    &:not(.@{upload-prefix-cls}-disabled):hover {\n      border-color: @primary-5;\n    }\n\n    p.@{upload-prefix-cls}-drag-icon {\n      .@{iconfont-css-prefix} {\n        color: @primary-5;\n        font-size: 48px;\n      }\n\n      margin-bottom: 20px;\n    }\n    p.@{upload-prefix-cls}-text {\n      margin: 0 0 4px;\n      color: @heading-color;\n      font-size: @font-size-lg;\n    }\n    p.@{upload-prefix-cls}-hint {\n      color: @text-color-secondary;\n      font-size: @font-size-base;\n    }\n\n    .@{iconfont-css-prefix}-plus {\n      color: @disabled-color;\n      font-size: 30px;\n      transition: all 0.3s;\n      &:hover {\n        color: @text-color-secondary;\n      }\n    }\n    &:hover .@{iconfont-css-prefix}-plus {\n      color: @text-color-secondary;\n    }\n  }\n\n  &-picture-card-wrapper {\n    .clearfix();\n\n    display: inline-block;\n    width: 100%;\n  }\n}\n\n.@{upload-prefix-cls}-list {\n  .reset-component();\n  .clearfix();\n  line-height: @line-height-base;\n\n  // ============================ Item ============================\n  &-item {\n    position: relative;\n    height: @line-height-base * @font-size-base;\n    margin-top: @margin-xs;\n    font-size: @font-size-base;\n    &-name {\n      display: inline-block;\n      width: 100%;\n      padding-left: @font-size-base + 8px;\n      overflow: hidden;\n      line-height: @line-height-base;\n      white-space: nowrap;\n      text-overflow: ellipsis;\n    }\n\n    &-card-actions {\n      position: absolute;\n      right: 0;\n\n      &-btn {\n        opacity: 0;\n      }\n      &-btn.@{ant-prefix}-btn-sm {\n        height: 20px;\n        line-height: 1;\n      }\n\n      &.picture {\n        top: 22px;\n        line-height: 0;\n      }\n\n      &-btn:focus,\n      &.picture &-btn {\n        opacity: 1;\n      }\n\n      .@{iconfont-css-prefix} {\n        color: @upload-actions-color;\n      }\n    }\n\n    &-info {\n      height: 100%;\n      padding: 0 4px;\n      transition: background-color 0.3s;\n\n      > span {\n        display: block;\n        width: 100%;\n        height: 100%;\n      }\n\n      .@{iconfont-css-prefix}-loading,\n      .@{upload-prefix-cls}-text-icon {\n        .@{iconfont-css-prefix} {\n          position: absolute;\n          top: (@font-size-base / 2) - 2px;\n          color: @text-color-secondary;\n          font-size: @font-size-base;\n        }\n      }\n    }\n\n    .@{iconfont-css-prefix}-close {\n      position: absolute;\n      top: 6px;\n      right: 4px;\n      color: @text-color-secondary;\n      font-size: 10px;\n      line-height: 0;\n      cursor: pointer;\n      opacity: 0;\n      transition: all 0.3s;\n\n      &:hover {\n        color: @text-color;\n      }\n    }\n\n    &:hover &-info {\n      background-color: @item-hover-bg;\n    }\n\n    &:hover .@{iconfont-css-prefix}-close {\n      opacity: 1;\n    }\n\n    &:hover &-card-actions-btn {\n      opacity: 1;\n    }\n\n    &-error,\n    &-error .@{upload-prefix-cls}-text-icon > .@{iconfont-css-prefix},\n    &-error &-name {\n      color: @error-color;\n    }\n\n    &-error &-card-actions {\n      .@{iconfont-css-prefix} {\n        color: @error-color;\n      }\n      &-btn {\n        opacity: 1;\n      }\n    }\n\n    &-progress {\n      position: absolute;\n      bottom: -12px;\n      width: 100%;\n      padding-left: @font-size-base + 12px;\n      font-size: @font-size-base;\n      line-height: 0;\n    }\n  }\n\n  // =================== Picture & Picture Card ===================\n  &-picture,\n  &-picture-card {\n    .@{upload-item} {\n      position: relative;\n      height: 66px;\n      padding: @padding-xs;\n      border: @border-width-base @upload-picture-card-border-style @border-color-base;\n      border-radius: @border-radius-base;\n\n      &:hover {\n        background: transparent;\n      }\n\n      &-error {\n        border-color: @error-color;\n      }\n    }\n\n    .@{upload-item}-info {\n      padding: 0;\n    }\n\n    .@{upload-item}:hover .@{upload-item}-info {\n      background: transparent;\n    }\n\n    .@{upload-item}-uploading {\n      border-style: dashed;\n    }\n\n    .@{upload-item}-thumbnail {\n      width: 48px;\n      height: 48px;\n      line-height: 54px;\n      text-align: center;\n      opacity: 0.8;\n\n      .@{iconfont-css-prefix} {\n        font-size: 26px;\n      }\n    }\n\n    // Adjust the color of the error icon : https://github.com/ant-design/ant-design/pull/24160\n    .@{upload-item}-error .@{upload-item}-thumbnail {\n      .@{iconfont-css-prefix} {\n        svg path {\n          &[fill='#e6f7ff'] {\n            fill: color(~`colorPalette('@{error-color}', 1) `);\n          }\n\n          &[fill='#1890ff'] {\n            fill: @error-color;\n          }\n        }\n      }\n    }\n\n    .@{upload-item}-icon {\n      position: absolute;\n      top: 50%;\n      left: 50%;\n      font-size: 26px;\n      transform: translate(-50%, -50%);\n\n      .@{iconfont-css-prefix} {\n        font-size: 26px;\n      }\n    }\n\n    .@{upload-item}-image {\n      max-width: 100%;\n    }\n\n    .@{upload-item}-thumbnail img {\n      display: block;\n      width: 48px;\n      height: 48px;\n      overflow: hidden;\n    }\n\n    .@{upload-item}-name {\n      display: inline-block;\n      box-sizing: border-box;\n      max-width: 100%;\n      margin: 0 0 0 8px;\n      padding-right: 8px;\n      padding-left: 48px;\n      overflow: hidden;\n      line-height: 44px;\n      white-space: nowrap;\n      text-overflow: ellipsis;\n      transition: all 0.3s;\n    }\n\n    .@{upload-item}-uploading .@{upload-item}-name {\n      line-height: 28px;\n    }\n\n    .@{upload-item}-progress {\n      bottom: 14px;\n      width: ~'calc(100% - 24px)';\n      margin-top: 0;\n      padding-left: 56px;\n    }\n\n    .@{iconfont-css-prefix}-close {\n      position: absolute;\n      top: 8px;\n      right: 8px;\n      line-height: 1;\n      opacity: 1;\n    }\n  }\n\n  // ======================== Picture Card ============