import React from 'react';
import "d3-scale";
import _ from "lodash";

import './Base.less'

export default class Base extends React.Component {

    setInput = (array) => {
        this.input = _.clone(array);
    }

    setOptions = (options) => {
        this.duration = options.duration || this.duration
        this.highlightColors = options.highlightColors || this.highlightColors
    }
}
