import React from 'react';
import * as d3 from "d3";
import "d3-scale";
import _ from "lodash";

import Base from "./Base"

export default class BarChart extends Base {

    plot = () => {
        this.svgRef.innerHTML = ""

        let {width, height} = this.svgRef.getBoundingClientRect()

        const svg = d3.select(this.svgRef).append("g")
        const data = this.input.map((e, i) => ({...e, index: i}))

        const pie = d3.pie().sort(null).value(() => 1);
        const arc = d3.arc()
            .innerRadius(this.isDonut ? Math.min(width, height) / 4 - 1 : 0)
            .outerRadius(Math.min(width, height) / 2 - 1)

        let g = svg.append("g")
            .attr("transform", "translate(" + width / 2 + "," + height / 2 + ")");

        let arcs = g.selectAll("arc")
            .data(pie(data))
            .enter()
            .append("g");

        arcs.append("path")
            .attr("style", ({data}) => Object.entries(data.style).map(([k, v]) => `${k}:${v}`).join(';'))
            .attr("index", ({index}) => index)
            .attr("o-fill", ({data}) => data.style.fill)
            .attr("d", arc);

        this.allPaths = _.range(0, data.length).map(e => {
            return svg.select(`[index='${e}']`)
        })
    }

    highlight = (index, colorIndex = 0) =>{
        if (colorIndex > this.highlightColors.length){
            colorIndex = 0
        }
        this.allPaths[index].style("fill", this.highlightColors[colorIndex])
    }

    unHighlight = (index) =>{
        this.allPaths[index].style("fill", this.allPaths[index].attr("o-fill"))
    }

    swap = async (index1, index2) => {

        if (index1 === index2) return;

        if (this._animateSwapDuration !== this.duration){
            clearInterval(this._animateInterval)

            this._animateInterval = setInterval(()=>{
                this._animateSwapSleepingCallback?.();
            }, this.duration)

            this._animateSwapDuration = this.duration;
        }


        let path1 = this.allPaths[index1];
        let path2 = this.allPaths[index2];

        let c1 = path1.attr("o-fill")
        let c2 = path2.attr("o-fill")

        if (this.duration >= 100){
            await Promise.all([
                path1.transition().duration(this.duration).style("fill", c2).end(),
                path2.transition().duration(this.duration).style("fill", c1).end()
            ])
        } else {
            path1.style("fill", c2)
            path2.style("fill", c1)

            await new Promise(r => this._animateSwapSleepingCallback = r)
        }

        path1.attr("o-fill", c2)
        path2.attr("o-fill", c1)

        this.allPaths[index1] = path1;
        this.allPaths[index2] = path2;

        let tmp = this.input[index1];
        this.input[index1] = this.input[index2];
        this.input[index2] = tmp;
    }

    render() {
        return (
            <svg ref={(ref) => this.svgRef = ref} className={"display-svg"}/>
        )
    }
}
