import _ from "lodash";
import gradient from "gradient-color"
import {SwatchesPicker} from "react-color";
import {proxy, useSnapshot} from 'valtio'
import tinycolor from "tinycolor2"
import React from "react";

const state = proxy({
    solidColor: "#000000"
})

class SelectColor extends React.Component{
    state = {
        color : "#000000"
    }
    render(){
        return (
            <SwatchesPicker color={this.state.color} onChangeComplete={color => {
                state.solidColor = color.hex
                this.setState({
                    color: color.hex
                })
            }}/>
        )
    }
}

export default [
    {
        name: "Solid",
        parse: (data) => {
            return data.map(e => ({value: e, style: {fill: state.solidColor}, rgb : tinycolor(state.solidColor).toRgb()}))
        },
        configuration: <SelectColor/>
    },
    {
        name: "Rainbow",
        parse: (data) => {

            const colors = gradient(["violet", "indigo", "blue", "green", "yellow", "orange", "red", "violet"], data.length + 1)

            let assignedColors = _.clone(data).sort((a, b) => a - b).map((e, i) => ({
                value: e,
                color: colors[i]
            })).reduce((a, b) => {
                a[b.value] = b.color
                return a
            }, {})

            return data.map(e => ({value: e, style: {fill: assignedColors[e]}, rgb: tinycolor(assignedColors[e]).toRgb()}))
        }
    }
]
