import React from 'react';
import "d3-scale";
import { DownOutlined } from '@ant-design/icons';

import random from 'random';
import BarChart from "../components/visualization/BarChart";
import "./Sorting.less"
import {Button, Dropdown, Menu, Popover, Slider, Space} from "antd";

import VisualizationComponents from "../components/visualization/index";
import SortingAlgorithms from "../sorting-alg/index"
import InputGenerators from "../utils/input-generator";
import ColorSchemes from "../utils/color-scheme"
import SortBy from "../utils/sort-by"

export default class Sorting extends React.Component {

    state = {
        inputGenerator: InputGenerators[0],
        sortingAlgorithm: SortingAlgorithms[0],
        inputSize: 100,
        colorScheme: ColorSchemes[1],
        animationDuration: 10,
        visualizationComponent : VisualizationComponents[0],
        sortBy: SortBy[0]
    }

    input = []

    componentDidMount() {
        this.randInput()
    }

    randInput = async () =>{

        this.sortingAlg && await this.sortingAlg.stop();

        this.input = this.state.inputGenerator.generate(this.state.inputSize)

        // if (this.state.colorScheme.name === "Solid"){
        //     this.input = this.state.colorScheme.parse(this.input, "indianred")
        // } else if (this.state.colorScheme.name === "Rainbow"){
        //     this.input = this.state.colorScheme.parse(this.input)
        // }

        this.input = this.state.colorScheme.parse(this.input)

        this.dataVis.setInput(this.input)

        this.dataVis.plot()
    }

    resizeHandle = async () => {
        if (!this.sortingAlg?.isPaused){
            this.sortingAlg && await this.sortingAlg.pause()
            this.dataVis.plot()
            this.sortingAlg && await this.sortingAlg.resume()
        } else {
            this.dataVis.plot()
        }

    }

    sort = async ()=>{

        if (this.sortingAlg && await this.sortingAlg.stop()){
            return this.randInput()
        }

        this.dataVis.setOptions({
            duration : this.state.animationDuration,
            highlightColors : ["black", "black"]
        })

        this.sortingAlg = new this.state.sortingAlgorithm.construct({
            input : this.input,
            vis : this.dataVis,
            options : {
                compareFunc : this.state.sortBy.func
            }
        })

        await this.sortingAlg.sort()
    }

    render() {

        const VisualizationComponent = this.state.visualizationComponent.component

        return (
            <div className={"sorting-page-container"}>
                <iframe title={"resize-sensor"} ref={(ref) => {
                    ref?.contentWindow.addEventListener('resize', ()=>{
                        window.requestAnimationFrame(this.resizeHandle)
                    })
                }} style={{height: 0, width: '100%', border: 'none', background: 'transparent',  zIndex: -1, top: 0, left: 0, pointerEvents: "none",}}/>
                <VisualizationComponent ref={ref => {
                    this.dataVis = ref;
                }}/>

                <Space className={"sorting-action"}>

                    <Dropdown.Button overlay={(
                        <Menu onClick={(e)=>{
                            this.setState({
                                inputGenerator: InputGenerators[Number(e.key)]
                            }, ()=>{
                                this.randInput()
                            })
                        }}>
                            {
                                InputGenerators.map(({name}, i) => (
                                    <Menu.Item key={i}>
                                        {name}
                                    </Menu.Item>
                                ))
                            }

                        </Menu>
                    )} icon={<DownOutlined />} onClick={this.randInput}>
                        {this.state.inputGenerator.name}
                    </Dropdown.Button>

                    <Dropdown overlay={(
                        <Menu onClick={(e)=>{
                            this.setState({
                                sortingAlgorithm: SortingAlgorithms[Number(e.key)]
                            })
                        }}>
                            {
                                SortingAlgorithms.map(({name}, i) => (
                                    <Menu.Item key={i}>
                                        {name}
                                    </Menu.Item>
                                ))
                            }

                        </Menu>
                    )} icon={<DownOutlined />}>
                        <Button>
                            {this.state.sortingAlgorithm.name} <DownOutlined />
                        </Button>
                    </Dropdown>

                    <Dropdown overlay={<Menu onClick={(e)=>{
                        this.setState({
                            inputSize : Number(e.key)
                        })
                    }}>
                        {
                            [10, 20, 50, 100, 200, 500, 1000, 2000].map(size => (
                                <Menu.Item key={size}>
                                    {size} data points
                                </Menu.Item>
                            ))
                        }

                    </Menu>}>
                        <Button>
                            {this.state.inputSize} data points <DownOutlined />
                        </Button>
                    </Dropdown>

                    <Dropdown overlay={(
                        <Menu onClick={(e)=>{
                            this.setState({
                                colorScheme: ColorSchemes[Number(e.key)]
                            })
                        }}>
                            {
                                ColorSchemes.map(({name, configuration}, i) => !configuration ? (
                                    <Menu.Item key={i}>
                                        {name}
                                    </Menu.Item>
                                ) : (
                                    <Menu.Item key={i}>
                                        <Popover placement="right" content={configuration} title={null} overlayClassName={"color-scheme-config-popover"}>
                                            <div style={{width: '100%'}}>
                                                {name}
                                            </div>
                                        </Popover>
                                    </Menu.Item>

                                ))
                            }

                        </Menu>
                    )} icon={<DownOutlined />}>
                        <Button>
                            {this.state.colorScheme.name} <DownOutlined />
                        </Button>
                    </Dropdown>

                    <Dropdown overlay={(
                        <Menu onClick={(e)=>{
                            this.setState({
                                visualizationComponent: VisualizationComponents[Number(e.key)]
                            }, ()=>{
                                this.randInput()
                            })
                        }}>
                            {
                                VisualizationComponents.map(({name}, i) => (
                                    <Menu.Item key={i}>
                                        {name}
                                    </Menu.Item>
                                ))
                            }

                        </Menu>
                    )} icon={<DownOutlined />}>
                        <Button>
                            {this.state.visualizationComponent.name} <DownOutlined />
                        </Button>
                    </Dropdown>

                    {/*<Dropdown overlay={(*/}
                    {/*    <Menu onClick={(e)=>{*/}
                    {/*        this.setState({*/}
                    {/*            sortBy: SortBy[Number(e.key)]*/}
                    {/*        }, ()=>{*/}
                    {/*            this.randInput()*/}
                    {/*        })*/}
                    {/*    }}>*/}
                    {/*        {*/}
                    {/*            SortBy.map(({name}, i) => (*/}
                    {/*                <Menu.Item key={i}>*/}
                    {/*                    {name}*/}
                    {/*                </Menu.Item>*/}
                    {/*            ))*/}
                    {/*        }*/}

                    {/*    </Menu>*/}
                    {/*)} icon={<DownOutlined />}>*/}
                    {/*    <Button>*/}
                    {/*        {this.state.sortBy.name} <DownOutlined />*/}
                    {/*    </Button>*/}
                    {/*</Dropdown>*/}

                    <Dropdown overlay={<Menu onClick={(e)=>{
                        this.setState({
                            animationDuration : Number(e.key)
                        })
                        this.dataVis.setOptions({duration : Number(e.key)})
                    }}>
                        {
                            [1, 10, 20, 50, 100, 200, 500].map(duration => (
                                <Menu.Item key={duration}>
                                    {duration}
                                </Menu.Item>
                            ))
                        }

                    </Menu>}>
                        <Button>
                            Speed: {this.state.animationDuration} <DownOutlined />
                        </Button>
                    </Dropdown>

                    <Button onClick={this.sort}>
                        Sort
                    </Button>

                    <Button onClick={()=>{
                        this.sortingAlg.pause();
                    }}>
                        Pause
                    </Button>

                    <Button onClick={()=>{
                        this.sortingAlg.resume();
                    }}>
                        Resume
                    </Button>

                </Space>
            </div>
        )
    }
}
