export default class BaseSortClass{

    constructor({input, options = {}, vis}){
        this.input = input;
        this.options = options;
        this.vis = vis;

        this.isStarted = false;
        this.isPaused = false;
        this.isDone = false;
        this.isStopped = false;

        this.checkPaused = () => {}
        this.checkStopped = () => {}

        if (options.compareFunc){
            this.compareFunc = options.compareFunc;
        } else {
            this.compareFunc = (a, b) => a - b;
        }
    }

    _sort = async () => {}

    sort = async () =>{
        this.isStarted = true;
        this.isPaused = false;

        await this._sort();
        this.isDone = true;
    }

    pause = async () => {
        if (this.isStarted && !this.isDone && !this.isPaused) {
            await new Promise(r => {
                this.checkPaused = ()=>{
                    return new Promise(r1 => {
                        this.checkPausedCallback = r1
                        r()
                    })
                }
            })
        }
        this.isPaused = true;
    }

    resume = async () => {
        this.isPaused = false;
        this.checkPausedCallback?.();
        this.checkPaused = () => {}
    }

    stop = async () => {
        if (!this.isStarted || this.isStopped || this.isDone) return false;

        this.isStopped = true;

        if (this.isPaused) {
            await this.resume()
        }

        await new Promise(r => {
            this.checkStopped = ()=>{
                r();
                this.checkStopped = () => {}
                return true;
            }
        })

        return true
    }
}
