import React from 'react';
import * as d3 from "d3";
import "d3-scale";
import _ from "lodash";

import Base from "./Base"

export default class BarChart extends Base {

    plot = () => {
        this.svgRef.innerHTML = ""

        let {width, height} = this.svgRef.getBoundingClientRect()

        const x = d3.scaleBand().range([0, width]).padding(this.isColorBar ? 0 : 0.1);
        const y = d3.scaleLinear().range([height, 0]);

        const svg = d3.select(this.svgRef).append("g")
        const data = this.input.map((e, i) => ({...e, index: i}))

        x.domain(_.range(0, data.length));
        y.domain([0, d3.max(data.map(e => this.isColorBar ? 1 : e.value))]);

        svg.selectAll(".bar")
            .data(data)
            .enter().append("rect")
            .attr("style", ({style}) => Object.entries(style).map(([k, v]) => `${k}:${v}`).join(';'))
            .attr("index", ({index}) => index)
            .attr("x", ({index}) => x(index))
            .attr("width", x.bandwidth())
            .attr("y", ({value}) => this.isColorBar ? 1: y(value))
            .attr("height", ({value}) => height - y(value));

        this.allBars = _.range(0, data.length).map(e => {
            return svg.select(`[index='${e}']`)
        })
    }

    highlight = (index, colorIndex = 0) =>{
        if (colorIndex > this.highlightColors.length){
            colorIndex = 0
        }

        // in case of highlight multiple time
        if (!this.allBars[index].attr("old-fill")){
            this.allBars[index].attr("old-fill", this.allBars[index].style("fill"))
        }
        this.allBars[index].style("fill", this.highlightColors[colorIndex])
    }

    unHighlight = (index) =>{
        this.allBars[index].style("fill", this.allBars[index].attr("old-fill"))
    }

    swap = async (index1, index2) => {

        if (index1 === index2) return;

        if (this._animateSwapDuration !== this.duration){
            clearInterval(this._animateInterval)

            this._animateInterval = setInterval(()=>{
                this._animateSwapSleepingCallback?.();
            }, this.duration)

            this._animateSwapDuration = this.duration;
        }


        let bar1 = this.allBars[index1];
        let bar2 = this.allBars[index2];

        let x1 = bar1.attr("x")
        let x2 = bar2.attr("x")

        if (this.duration >= 100){
            await Promise.all([
                bar1.transition().duration(this.duration).attr("x", x2).end(),
                bar2.transition().duration(this.duration).attr("x", x1).end()
            ])
        } else {
            bar1.attr("x", x2)
            bar2.attr("x", x1)

            await new Promise(r => this._animateSwapSleepingCallback = r)
        }

        this.allBars[index1] = bar2;
        this.allBars[index2] = bar1;

        let tmp = this.input[index1];
        this.input[index1] = this.input[index2];
        this.input[index2] = tmp;
    }

    render() {
        return (
            <svg ref={(ref) => this.svgRef = ref} className={"display-svg"}/>
        )
    }
}
