import _ from "lodash";

export default [
    {
        name : "Default",
        func : (a, b) => a.value - b.value
    },
    ..._.uniq(_.range(1,100).map(() => _.sampleSize(['r','g','b'], 3).join(''))).sort().reverse().map(e => {
        let [x, y, z] = e.split('')
        return {
            name : `Color ${x.toUpperCase()}-${y.toUpperCase()}-${z.toUpperCase()}`,
            func : (a, b) => a.rgb[x] * 1000000 + a.rgb[y] * 1000 + a.rgb[z] - (b.rgb[x] * 1000000 + b.rgb[y] * 1000 + b.rgb[z])
        }
    })
]
