import BaseSortClass from "./Base";

export default class InsertionSort extends BaseSortClass{

    _sort = async ()=>{

        for (let i = 1; i < this.input.length; i++) {
            this.vis.highlight(i)

            let key = this.input[i];
            let j = i - 1;

            while (j >= 0 && this.compareFunc(this.input[j], key) > 0) {

                await this.checkPaused()
                if (this.checkStopped()) return;

                this.input[j + 1] = this.input[j];

                await this.vis.swap(j, j + 1);

                j = j - 1;
            }

            this.vis.unHighlight(j + 1)

            this.input[j + 1] = key;
        }
    }
}
