import BaseSortClass from "./Base";

export default class BubbleSort extends BaseSortClass{

    _sort = async ()=>{
        for (let i = 0; i < this.input.length; i++) {
            for (let j = 0; j < this.input.length - 1; j++) {

                await this.checkPaused()
                if (this.checkStopped()) return;

                this.vis.highlight(j)

                if (this.compareFunc(this.input[j], this.input[j + 1]) > 0) {
                    let tmp = this.input[j];
                    this.input[j] = this.input[j + 1];
                    this.input[j + 1] = tmp;

                    await this.vis.swap(j, j + 1);
                    this.vis.unHighlight(j + 1)
                } else {
                    this.vis.unHighlight(j)
                }
            }
        }
    }
}
