import BaseSortClass from "./Base";

export default class QuickSort extends BaseSortClass {

    _sort = async () => {

        let swap = async (items, leftIndex, rightIndex) => {

            await this.checkPaused()
            if (this.checkStopped()) return;

            const temp = items[leftIndex];
            items[leftIndex] = items[rightIndex];
            items[rightIndex] = temp;

            if (!this.isStopped){
                await this.vis.swap(leftIndex, rightIndex);
            }
        }

        let partition = async (items, left, right) => {
            await this.checkPaused()
            if (this.checkStopped()) return;

            let pivotIndex = Math.floor((right + left) / 2);
            const pivot = items[pivotIndex];

            let i = left, j = right;

            this.vis.highlight(left)
            this.vis.highlight(right)
            this.vis.highlight(pivotIndex, 1)

            while (i <= j) {
                while (this.compareFunc(items[i], pivot) < 0) {
                    i++;
                }
                while (this.compareFunc(items[j], pivot) > 0) {
                    j--;
                }
                if (i <= j) {

                    if (pivotIndex === i || pivotIndex === j){
                        this.vis.unHighlight(pivotIndex)
                    }

                    if (left === i || left === j){
                        this.vis.unHighlight(left)
                    }

                    if (right === i || right === j){
                        this.vis.unHighlight(right)
                    }

                    await swap(items, i, j);

                    this.vis.highlight(left)
                    this.vis.highlight(right)
                    this.vis.highlight(pivotIndex, 1)

                    i++; j--;
                }
            }

            this.vis.unHighlight(pivotIndex)
            this.vis.unHighlight(left)
            this.vis.unHighlight(right)

            return i;
        }

        let quickSort = async (items, left, right) => {
            await this.checkPaused()
            if (this.checkStopped()) return;

            let index;
            if (items.length > 1) {
                index = await partition(items, left, right); //index returned from partition
                if (left < index - 1) { //more elements on the left side of the pivot
                    await quickSort(items, left, index - 1);
                }
                if (index < right) { //more elements on the right side of the pivot
                    await quickSort(items, index, right);
                }
            }
        }

        await quickSort(this.input, 0, this.input.length - 1);
    }
}
